<script>
import Vue from "vue";
import Component from "vue-class-component";
import footerBar from "COMPONENTS/index/footerBar.vue";
import backTop from "COMPONENTS/index/backTop";
import axios from "axios";
import wechatMixin from "@/mixins/wechat";
@Component({
  components: {
    footerBar,
    backTop
  },
  mixins: [wechatMixin]
})
export default class PageIndex extends Vue {
  // 是否显示返回顶部按钮
  showBackTop = false;
  // 是否像是悬浮框
  showBar = true;
  // 是否首次到达动画框位置
  isFirstReach = false;
  // cdn 图片路径
  cdnPath = `${process.env.VUE_APP_CDNPATH}/website/`;
  // 分享的数据
  infos = {};

  baidu() {
    var head = document.getElementsByTagName("head")[0];
    var script = document.createElement("script");
    script.src = "https://hm.baidu.com/hm.js?fe10327641b63e67d706190cfb16c041";
    head.appendChild(script);
  }
  consult() {
    window.location.href = "https://www.wjx.top/jq/41711237.aspx";
  }
  //数字自增到某一值动画参数（目标元素,自定义配置
  numberAutoPlusAnimation(targetEle, params) {
    const options = params || {};
    let $this = document.querySelector(targetEle),
      time = options.time, //总时间--毫秒为单位
      finalNum = options.num, //要显示的真实数值
      regulator = options.regulator || 100, //调速器，改变regulator的数值可以调节数字改变的速度
      step = finalNum / (time / regulator) /*每30ms增加的数值--*/,
      count = options.initial, //计数器
      initial = options.initial;
    const timer = setInterval(() => {
      count = count + step;
      if (count >= finalNum) {
        clearInterval(timer);
        count = finalNum;
      } //避免调用text函数，提高DOM性能
      //t未发生改变的话就直接返回
      var t = Math.floor(count);
      if (t === initial) return;
      initial = t;
      $this.innerHTML = initial;
    });
  }

  numberAnimation() {
    this.numberAutoPlusAnimation(".num1", {
      time: 2000,
      num: 33,
      initial: 17,
      // regulator: 3,
      regulator: parseInt(Math.random() + 5)
    });
    this.numberAutoPlusAnimation(".num2", {
      time: 2000,
      num: 50,
      initial: 25,
      // regulator: 3,
      regulator: parseInt(Math.random() + 5)
    });
    this.numberAutoPlusAnimation(".num3", {
      time: 2000,
      num: 80,
      initial: 50,
      // regulator: 2,
      regulator: parseInt(Math.random() + 5)
    });
  }
  /**
   * @Author   小书包
   * @DateTime 2018-10-26
   * @detail   获取当前滚动的高度
   * @return   {[type]}   [description]
   */
  getNowScrollTop() {
    let scrollTop = 0;
    if (document.documentElement && document.documentElement.scrollTop) {
      scrollTop = document.documentElement.scrollTop;
    } else if (document.body) {
      scrollTop = document.body.scrollTop;
    }
    return scrollTop;
  }
  /**
   * @Author   小书包
   * @DateTime 2018-10-26
   * @detail   获取客户端的视口高度
   * @return   {[type]}   [description]
   */
  getClientHeight() {
    let clientHeight = 0;
    if (document.body.clientHeight && document.documentElement.clientHeight) {
      clientHeight =
        document.body.clientHeight < document.documentElement.clientHeight
          ? document.body.clientHeight
          : document.documentElement.clientHeight;
    } else {
      clientHeight =
        document.body.clientHeight > document.documentElement.clientHeight
          ? document.body.clientHeight
          : document.documentElement.clientHeight;
    }
    return clientHeight;
  }
  /**
   * @Author   小书包
   * @DateTime 2019-03-04
   * @detail   添加网站的关键词和描述
   * @return   {[type]}   [description]
   */
  appendHead() {
    var head = document.getElementsByTagName('head')[0]
    // 添加关键词
    var meta_keywords = document.createElement('meta')
    meta_keywords.name = 'Keywords'
    meta_keywords.content = '猎多多，找工作，求职，招聘，精英人才，互联网精英，约面，社交，小程序找工作，面试，高端招聘，跳槽，换工作，互联网招聘，微信招聘，高薪职位，互联网行业，职场招聘， 猎头招聘，校招，社招，全职，兼职，实习'
    head.appendChild(meta_keywords)

    var meta_description = document.createElement('meta')
    meta_description.name = 'Description'
    meta_description.content = '猎多多以“人”为核心，通过社交约面的方式，让候选人和老板直接面谈，为中高端候选人提供更高效的求职面试服务，为招聘企业提供更多优质互联网精英人才。'
    head.appendChild(meta_description)
  }

  //判断是否是微信浏览器的函数
  get isWeiXin() {
    var ua = window.navigator.userAgent.toLowerCase();
    return ua.match(/MicroMessenger/i) == "micromessenger";
  }
  mounted() {
    let isMobile = /Mobile/i.test(navigator.userAgent);
    
    let timer = null;
    let numberHere = document.querySelector(".advisory").offsetTop;
    let numberBoxHeight = document.querySelector(".advisory").offsetHeight;
    let clientHeight = this.getClientHeight();

    if (!isMobile) {
      window.location.href = process.env.VUE_APP_PC;
      return;
    }

    this.baidu();
    this.appendHead()
    this.wechatShare({
      title: "猎多多丨精英人才招聘神器",
      desc: "直接约面 更快招人 约高端人才上猎多多",
      link: location.href.split("#")[0],
      imgUrl: `${this.cdnPath}share_img.png`
    });
    window.onscroll = () => {
      let scrollTop = this.getNowScrollTop();
      if (scrollTop >= numberHere + numberBoxHeight && !this.isFirstReach) {
        this.isFirstReach = true;
        this.numberAnimation();
      }

      if (window.scrollY >= document.body.clientHeight - window.innerHeight) {
        this.showBackTop = true;
      } else {
        this.showBackTop = false;
      }
      this.showBar = false;
      // clearTimeout(timer)
      // timer = setTimeout(() => this.showBar = true, 50)
      this.showBar = true;
    };
  }
}
</script>
